import React from 'react'
import { Radio, Row, Col } from 'antd'
import { connect } from 'react-redux'
import { handleChange } from '../redux/formulario/formulario.actions'

const RadioButtonOnsurance = ({handleChange, funcao, value}) => {
    return (
        <Radio.Group
            onChange={e => handleChange(e.target.value, funcao)}
            value={value}
            buttonStyle='solid'
            style={{width: '100%'}}
        >
            <Row type='flex' justify='center'>
                <Col span={12}>
                    <Radio.Button value={true}>Sim</Radio.Button>
                    <Radio.Button value={false}>Não</Radio.Button>
                </Col>
            </Row>
        </Radio.Group>
    )
}

const mapDispatchToProps = dispatch => ({
    handleChange: (item, tipo) => dispatch(handleChange(item, tipo))
})

export default connect(null, mapDispatchToProps)(RadioButtonOnsurance);